import { Node, LinkNode, QueueNode } from "./Node";

export class Link{
  header: LinkNode;
  length: number;

  constructor(){
    this.length = 0;
    this.header = null;
  }

  appendNode(node: LinkNode) {
    var current = null;
    // 如果头节点不存在
    if (!this.header) {
      // 给头节点赋予初值
      this.header = node;
    } else {
      // 头指针为初值
      current = this.header;
      // 便利循环头指针，找到最后一个指针
      while (current.next) {
        current = current.next
      }
      // 最后一个指针的下一个指针为当前node
      current.next = node;
    }

    this.length++;
  }

  /**
   * 删除一个节点，如果不传值则默认删除最后一个
   * @param {Integer} index - 删除的行数
   */
  removeNode(index:number) {
    if ((index < 0) || (index > this.length + 1)) {
      console.error('越界啦')
      return;
    }
    let t_index = index || this.length; // 目标索引
    let current = this.header; // 当前节点
    let c_index = 0; // 当前索引节点index
    var previous = null; // 用来记录上一个节点，当当前节点删除了，将上个节点指向下个节点

    if (index === 0) {
      current = this.header;
    } else {
      while (c_index++ < t_index) {
        previous = current;
        current = current.next;
      }
      previous.next = current.next;
    }

    this.length--;

    return this;
  }

  /**
   * 插入节点位置
   * @param {Integer} index - 节点插入位置
   * @param {LinkedNode} node - 节点
   */
  insertNode(index:number,node:LinkNode){
    if ((index < 0) || (index > this.length + 1)) {
      console.error('越界啦')
      return;
    }

    let current = this.header;
    let t_index = index;
    let c_index = 0;
    let previous = null;
    if(t_index === 0) {
      node.next = current;
      this.header = node;
    }else{
      while(c_index ++ < t_index){
        previous = current;
        current = current.next;
      }
      previous.next = node;
      node.next = current;
    }

    this.length ++;
    return this;
  }
}

export class Queue extends Link{
  footer: QueueNode;
  header: QueueNode;
  constructor(){
    super()
    this.header = null;
    this.footer = null;
  }

  setHeader(node:QueueNode){
    this.header = node;
  }

  setFooter(node:QueueNode){
    this.footer = node;
  }

  appendNode(node: QueueNode) {
    var current = null;
    // 如果头节点不存在
    if (!this.header) {
      // 给头节点赋予初值
      this.header = node;
    } else {
      // 头指针为初值
      current = this.header;
      // 便利循环头指针，找到最后一个指针
      while (current.next) {
        current = current.next
      }
      // 最后一个指针的下一个指针为当前node
      current.next = node;
    }

    this.length++;
  }
}